.slideshow {
    width: 100%;
    height: 75vh;

    text-align: center;
}

.slideshow div {
    background-color: inherit;
    /* margin: 0 auto; */
}

.slick-current {
    z-index: 20;
}

#hover-me {
    position: absolute;
    left: 5%;
    bottom: 2.5%;
    
    padding: 1% 2.5%;

    border: 1px solid aliceblue;
    border-radius: 5px;

    background-color: rgba(68, 68, 68, 0.8);
    color: aliceblue;

    -webkit-transition: all 0.45s ease;

    -o-transition: all 0.45s ease;

    transition: all 0.45s ease;
}

.slide-overlay {
    padding: 0vh 2.5%;
}

div.slide-overlay {
    position: absolute;
    left: 0;
    top: 0;

    width: 33%;
    height: 100%;
    background-color: rgba(68, 68, 68, 0.8);

    color: aliceblue;

    z-index: 10;
    -webkit-transition: all 0.45s ease;
    -o-transition: all 0.45s ease;
    transition: all 0.45s ease;
}

div.slide-overlay h2 {
    font-style: italic;
}

.hidden {
    opacity: 0 !important;
}

.hidden.slide-overlay {
    height: 0% !important;
}

.slide-overlay p {
    text-align: left;
}

div.my-slide {
    position: relative;
    height: 55vh;
}

.slick-slide {
    overflow: hidden;
}

.slick-track {
    margin: 0 auto;
}

.slick-slide img {
    /* margin: 0 auto; */
    height: 100%;
    width: 100%;
}

/* .slick-dots > li {
    display: inline;
} */

.slick-dots > li.slick-active > button::before {
    color: burlywood;
}


/* Okay, hear me out with these next two selectors.
    So, because of where each arrow is rendered within its 
    box, I have to slightly adjust their positioning so 
    that everything looks centered. Somehow this works fine
    in the default theme for the slick website,
    but not for me...
*/
.slick-prev {
    left: -2.5% !important;
}

.slick-next {
    right: -1.1% !important;
}

.slick-prev,
.slick-next {
    color: burlywood;
    border: none;
    outline: none;
    background: transparent;
    -webkit-transition: color 0.5s ease;
    -o-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.slick-arrow::before {
    color: burlywood;
}

.slick-prev::before,
.slick-next::before {
    font-size: 30px;
    color: burlywood;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
    color: #444;
    outline: none;
    background: transparent;
}

.slideshow .slick-dots li button {
    position: relative;
    top: 150%;
    width: 10px;
    height: 25px;
}

.slideshow .slick-dots li button::before {
    content: '';
    background: burlywood;
    color: burlywood;
    height: 20px;
    width: 3px;
    border-radius: 2px;
    position: absolute;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
        transform: translateY(-50%);
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    opacity: 0.6;
}

.slideshow .slick-dots li.slick-active button::before {
    height: 40px;
    opacity: 1;
}

.slideshow .slick-dots li button:hover::before {
    height: 30px;
    opacity: 0.8;
}


