header {
    position: fixed;
    top: 0;
    height: 10vh;
    width: 100vw;
    border-bottom: 1.5px solid burlywood;

    font-family: "Macondo Swash Caps", sans-serif;
    /* font-family: "Bubbler One", sans-serif; */

    background-color: #f7f7f7;
    color: #444;
    z-index: 10;
}

header.row {
    margin: 0 !important;
}

header div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 !important;
}

header div:first-of-type {
    -ms-flex-pack: center;
        justify-content: center;
}

header div:last-of-type {
    -ms-flex-pack: end;
        justify-content: flex-end;
}

header div h1 {
    margin: 0;
}

.drop-down {
    background: transparent;
    border: none;
}

.drop-down:hover {
    cursor: pointer;
}


